import BigNumber from 'bignumber.js'
import React from 'react';
import './App.css';
import styled from 'styled-components';
import 'react-responsive-modal/styles.css';
import { NftContract, TokenContract} from "../constants/index";
import TokenAbi from "../tokenAbi.json";
import NftAbi from "../nftAbi.json";
import Modal from "./Modal/Modal";

class NFTNAME extends React.Component <any, any> {

    constructor(props) {
        super(props)
        this.state = {
          account: '',
          loading: false,
          networkId: 0,
          ownerOf: false,
          balanceOf:0,
          MintActivated: false,
          transaction:'',
          tokenRewards: new BigNumber(0),
          nameChangePrice: new BigNumber(0),
          name: '',
          newname: '',
          isReserved: false,
          tokenId:0
        }
      }



  networkId = 1;
  bigNumber =   "1000000000000000000";
  NftContract = NftContract;
  NftApp = null;
  TokenApp = null;
  TokenContract = TokenContract;

  async componentWillMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidMount() {
    if(this.props.account !== this.state.account && this.props.web3 !== null)
      await this.loadContract()
  }

  async componentDidUpdate(prevProps) {
      if(this.props.account !== this.state.account && this.props.web3 !== null)
        await this.loadContract()
    }

 async loadContract() {
    this.NftApp = new this.props.web3.eth.Contract(NftAbi, this.NftContract);
    this.TokenApp = new this.props.web3.eth.Contract(TokenAbi, this.TokenContract);
    await this.connect()
  }

  async connect() {
    if(this.props.account){
        this.setState({ account: this.props.account })
        var ownerOf = (await this.NftApp.methods.ownerOf(this.props.match.params.tokenId).call())
        if (ownerOf === this.state.account)
            this.setState({
              ownerOf:  true
            })
        this.setState({
          MintActivated:  (await this.NftApp.methods.MintActivated().call()),
          tokenId:  this.props.match.params.tokenId,
          tokenRewards: new BigNumber(await this.TokenApp.methods.balanceOf(this.props.account).call()),
          nameChangePrice: new BigNumber(await this.NftApp.methods.nameChangePrice().call()),
          name: await this.NftApp.methods.tokenNameByIndex(this.props.match.params.tokenId).call()
        })
    }
  }


 async updateNFT(){
        let value = document.getElementById("walletToCheck")  as HTMLInputElement ;
        this.setState({newname:value.value})

        let reserved = await this.NftApp.methods.isNameReserved(value.value).call()
        if(reserved)
            this.setState({isReserved: true})
        else
            this.setState({isReserved: false})

     }


  resolve(transactionHash){
   this.setState({loading:true})
   //console.log(transactionHash,"transactionHash")
   this.setState({transaction:"https://etherscan.io/tx/"+transactionHash})
  }

  reject(err){
   this.setState({loading:false}); 
   //console.log(err)
  }

  loaderModal(receipt){
    this.setState({loading:false}); 
    //console.log(receipt['transactionHash'])
    this.connect()
  }
    

  closeModal(){
    this.setState({loading:false}); 
  }

 async claimToken(){

    await new Promise((resolve, reject) => {
       this.NftApp.methods.changeName(this.state.tokenId,this.state.newname)
      .send({from: this.state.account})
        .once('transactionHash', (transactionHash) => {
            this.resolve(transactionHash)
        })
        .on('confirmation', (confirmationNumber) => {
            //this.resolve(confirmationNumber)
        })
        .on('error', err => this.reject(err))
        .then( receipt =>
            this.loaderModal(receipt)
        );
    })
  }

ButtonDisable = ({disabled=this.state.isReserved}: {disabled: boolean}) => {
              return <ButtonClaim disabled={disabled} onClick={() => this.claimToken()}>{disabled ? 'Name Reserved' : `Apply Name Change`}</ButtonClaim>

}

    render() {
     if(!this.props.web3){
         return(
          <AppWrapper>
            <ConnectWalletWrapper>
                 <Title>
                 "Unlock Your Metamask or click Connect"
                  </Title>
            </ConnectWalletWrapper>
          </AppWrapper>
        )
      }
      else
    {
    return (
      <AppWrapper>
                <Modal title="Pending transaction" onClose={() => this.closeModal()} show={this.state.loading}>
                        <ButtonEther><StyledLinkH href={this.state.transaction}  rel="noreferrer" target="_blank">EtherScan transaction</StyledLinkH></ButtonEther>
                </Modal>
        {this.state.ownerOf && this.state.tokenRewards.gte(this.state.nameChangePrice) && 
            <ConnectWalletWrapper>
                  <Title>
                    "Change your {this.state.name ? this.state.name: "Dirty Panties"} #{this.state.tokenId} name."
                  </Title>
                  <Spacer/>
              <MintInput onChange={() => this.updateNFT()} id="walletToCheck" placeholder="Type a new name" />
              <Spacer/>
               <this.ButtonDisable />
              <Spacer/>
            </ConnectWalletWrapper>
        }
        {  (this.state.tokenRewards.lte(this.state.nameChangePrice) && this.state.ownerOf ) && 
            <ConnectWalletWrapper>
                  <Title>
                    "You don't have enough $PNM tokens to change the name."
                  </Title>
                  <Spacer/>
             </ConnectWalletWrapper>
        }

        {!this.state.ownerOf &&
            <ConnectWalletWrapper>
                  <Title>
                    "You aren't the Owner of {this.state.name ? this.state.name: "Dirty Panties"} #{this.state.tokenId}."
                  </Title>
               <Spacer/>
              </ConnectWalletWrapper>
       }

      </AppWrapper>
    );
    }
  }
}

const AppWrapper = styled.div`
    padding: 100px;
    display: flex;
    flex-direction: column;
    min-height: 10vh;

`

const Spacer = styled.div`
flex: 1;
`


const MintInput = styled.input`
  border-color: transparent;
  background-color: transparent;
  text-align: center;
  padding: 2px 5px;
  color:  white;
  font-size: 10px;
  border: 0px solid;
  border-radius: 7px;
  max-width: 30%;
  outline: 0;
  font-style: normal;
  font-weight: 400;
  line-height: 17px;

  width: 100%;

  @media only screen and (min-width: 768px) {
    padding: 20px 55px;
    font-size: 18px;
    width: 100%;
  }
`
const ConnectWalletWrapper = styled.div`
  margin-top: 25px;
  text-align: center;
`

const ButtonClaim = styled.button`
  border: 2px solid #D29EE9;
  border-radius: 7px;
  font-size: 8px;
  font-style: normal;
  font-weight: 700;
  line-height: 5px;
  color: white;
  padding: 8px  20px;
  background-color: transparent;
  letter-spacing: 2px;
  text-decoration: none;

  &:hover {
    border: 3px solid #D29EE9;
    outline: 5px solid #fff;
    outline-offset: -5px;
  }

  &:disabled{
    border: 3px solid #999999;
    background-color: #cccccc;
    color: #666666;
  }

  @media (min-width: 768px) {
    border: 3px solid #D29EE9;
    font-size: 10px;
    padding-left: 2px;
    padding-right: 2px;
    line-height: 19px;
    padding: 10px 85px;
  }
`

const Title = styled.text`
  font-size: 1.2em;
  text-align: center;
  #color: #fcd63594;
  color: white;
  font-size: 11px;
  font-weight: 700;

  background-color: transparent;
  letter-spacing: 1px;

  @media (min-width: 768px) {
    letter-spacing: 1px;
    font-size: 18px;
    padding-left: 2px;
    padding-right: 2px;
  }
`;

const StyledLinkH = styled.a`
  letter-spacing: 0px;
  color: #6230BB;
  font-style: normal;
  font-weight: 400;
  font-family: "AuroraMedium";
  font-size: 4vw;
  padding-left: 1px;
  padding-right:1px;
  text-decoration: none;
  &:hover {
    color: white;
  }
  &.active {
    color: white;
  }
  @media (min-width: 768px) {
    font-size: 1.2vw;
    font-weight: 400;
    padding-left: 5px;
    padding-right: 5px;
  }
`

const ButtonEther = styled.button`
  border: 2px solid #D29EE9;
  border-radius: 50px;
  font-size: 8px;
  font-style: normal;
  font-weight: 700;
  line-height: 5px;
  color: white;
  padding: 8px  14px;
  background-color: white;
  letter-spacing: 2px;
  text-decoration: none;

  &:hover {
    border: 3px solid #D29EE9;
    outline: 5px solid #fff;
    outline-offset: -5px;
    background-color: red;
  }

  @media (min-width: 768px) {
    border: 3px solid #D29EE9;
    font-size: 10px;
    padding-left: 2px;
    padding-right: 2px;
    line-height: 19px;
    padding: 10px 18px;
  }
`

export default NFTNAME;
